import React from 'react';
import {Col, Row} from "react-materialize";
import PreviewCard from "./PreviewCard";

const GridView = ({ItemList}) => {
    console.log(ItemList)
    //console.log(ItemList)
    return (
                <Row>
                    {ItemList.map((p)=>(
                        <Col
                            className="teal darken-1"
                            xl={4}
                            l={6}
                        >
                            <h1 style={{"font-size": "calc(24px + 16 * (120vw / 1280))", "textAlign": "center"}}>{p.name}</h1>
                            <img alt="Изображение" src={p.url} style={{width: '100%'}}></img>
                        </Col>
                    ))}
                </Row>
    );
};

export default GridView;
