import React from 'react';
import GridView from "../Main/VisualElements/GridView";
import brandsImageList from "../../data/brandsImageList";

export const BrandsPage = () => {
    return (
        <div>
            <h1 style={{"font-size": "calc(24px + 16 * (120vw / 1280))"}}>Бренды</h1>
            <GridView ItemList={brandsImageList}/>
        </div>
    );
};
