import React from 'react';
import {Col, Row} from "react-materialize";
import TestPreviewCard from "./TestPreviewCard";

const TestGridView = ({products}) => {
    return (
        <Row>
            {products.map((product) => (
                <Col
                    className="teal darken-1"
                    xl={4}
                    l={6}

                >
                    <TestPreviewCard key={product.id} product={product}/>
                </Col>
            ))}
        </Row>
    );
};

export default TestGridView;
