import React from 'react';
import GridView from "../Main/VisualElements/GridView";
import {gql, useQuery} from "@apollo/client";
import TestGridView from "../Main/VisualElements/TestGridView";

export const KnickersPage = () => {
    const MyQuery = gql`
    query MyQuery {
             categoryProducts(categoryName: "Трусики") {
                description
                id
                images {
                  url           
                }
            }
    }
`
    const {loading, error, data} = useQuery(MyQuery)

    if (loading) return <p>Loading...</p>
    if (error) return <p>Error</p>
    return (
        <div>
            <h1 style={{"font-size": "calc(24px + 16 * (120vw / 1280))"}}>Трусики</h1>
            <TestGridView products={data.categoryProducts}/>
        </div>
    );
};
