import React from 'react';
import GridView from "./VisualElements/GridView";
import categoriesImageList from "../../data/CategoriesImageList"

export const MainPage = () => {
    return (
        <div>
            <div>
                <h1 style={{"font-size": "calc(24px + 16 * (120vw / 1280))"}}>Группы товаров:</h1>
                <GridView ItemList={categoriesImageList}/>
            </div>
        </div>
    )
}
